@import "friendly.css";

body {
  font-family: '{{ site.fonts.family }}';
  font-size: '{{ site.fonts.size }}';
  margin: 10 10 10 10;
}

header {
  border-style: solid;
  border-width: 1px;
  border-color: black;
  width: 100%;
  height: 150px;
}


.current {
  color: green;
}

#menu {
  border-style: solid;
  border-width: 1px;
  border-color: black;
  height: 100%;
  /*width: 100%;*/
  padding: 5px 5px 5px 5px;
  margin: 5px 0px 5px 0px;
}

#course_sidebar {
  border-style: solid;
  border-width: 1px;
  border-color: black;
  height: 100%;
  width: 27%;
  float: right;
  padding: 5px 5px 5px 5px;
  margin: 5px 0px 5px 2px;
}

#content {
  border-style: solid;
  border-width: 1px;
  border-color: black;
  height: 100%;
  width: 70%;
  float: left;
  padding: 5px 5px 5px 5px;
  margin: 5px 2px 5px 0px;
}
